body{
    margin: 0;
    padding: 2rem;
    color:#ccc;
    text-align: center;
    font-family: sans-serif;
    background: #222;
}
*{
    box-sizing: border-box;
}

.container{
    max-width: 900px;
    width: 100%;
    margin: 2rem auto;
    text-align: left;
}
h1{
    padding: 1rem;
}

button{
  background: transparent;
  color:#ccc;
  text-align: center;
  border-radius: 10px;
  border:2px solid white;
  padding: .7rem;
  display: inline-block;
  margin: 1rem;
  font-size: 1.2rem;
  cursor: pointer;
}
button:focus{
    outline: none;
}
.spinner{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

button:disabled{
     opacity: .2;
}

.spinner{
    
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        padding: 0;
        margin: 0;
      }



.card{
    padding: 8px 16px;
    background: #1b1b1b;
    margin: 16px 0;
    border-radius: 20px;
}
.card p{
  margin: 6px 0;
  color:#999;
}
.card h3{
    margin: 10px 0;
    color:#ffff57;
}

form{
    width: 100%;
    padding: 2rem;
    margin: 2r;
    text-align: center;
}
form input{
    margin: 0 auto;
    padding: .7rem;
    border:none;
    border-radius: 10px;
    display: block;
    width: 60%;
    margin: auto;
    outline: none;
    font-size: 1rem;
    font-weight: bold;
}
